.container-fluid {
    max-width: 1400px;
}
.FooterComponent {
    min-height: 300px;
    background-color: #d3d8eb;
    margin-top: 20px;
}


.personal-details {
    margin-top: 20px;
    padding: 20px;
}

.footer-name{
    font-weight: 700;
    color: #5680E9;
}

.footer-subtext {
    font-weight: 500;
}

.custom-links {
    margin-top: 20px;
    padding: 20px;
}

.custom-button {
    border: none;
    text-decoration: none;
    font-weight: 500;
    background-color: #d3d8eb;;
    padding: 5px 15px;
    margin-right: 20px;
    border-radius: 5px;
    transition: 0.5s;
    border: 1px solid #84ceeb;
    margin-top: 10px;
}

.custom-button:hover{
    background-color: #84ceeb;
}

a {
    text-decoration: none;
    color: inherit;
}

footer {
    position: inherit;
}