.post {
    padding-left: 15px;
}
.custom-background {
    background: #00B4DB;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #0083B0, #00B4DB);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #0083B0, #00B4DB); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    min-height: 300px;
    border-radius: 5px;
}
.subtext{
    margin-top: 20px;
    font-weight: 500;
}

.href-links {
    font-weight: 700;
    color: #5680E9;
}

.callout {
    padding: 40px;
    background-color: #C1C8E4;
    border-radius: 10px;
    width: 100%;
    font-weight: 500;
    margin-bottom : 20px
}

.center {
    display: grid;
    place-items: center;
    margin-top: 50px;
}

.time {
    font-weight: 300;
}